<template>
  <div id="synchronous_advertising_guidance">
    <div class="leftMenu">
      <el-form style="width:300px" :model="form" label-width="80px">
        <el-form-item label="渠道">
          <el-select
            filterable
            style="width:100%"
            v-model="form.channel"
            placeholder="请选择渠道"
            @change="changeOpt('channel')"
          >
            <el-option value="all" label="全部"></el-option>
            <el-option
              v-for="(item, i) in channels"
              :key="i"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="国家">
          <el-select
            filterable
            @change="changeOpt('country')"
            style="width:100%"
            v-model="form.country"
            placeholder="请选择渠道"
          >
            <el-option value="all" label="全部"></el-option>
            <el-option
              v-for="(item, i) in countrys"
              :key="i"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="站点">
          <el-select
            filterable
            @change="changeOpt('store')"
            style="width:100%"
            v-model="form.store"
            placeholder="请选择站点"
          >
            <el-option value="all" label="全部"></el-option>
            <el-option
              v-for="(item, i) in stations"
              :key="i"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
    </div>
    <div class="rightTable">
      <div class="today_guide_price">
        <div>
          <table>
            <thead>
              <tr>
                <th colspan="3">
                  今日指导价
                </th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td></td>
                <td>自动</td>
                <td>手动</td>
              </tr>
              <tr>
                <td colspan="3">魔片</td>
              </tr>
              <tr v-for="item in mp_list_form" :key="item.spu">
                <td>{{ item.spu }}</td>
                <td>
                  <el-input
                    @keydown.native="inputNum"
                    @input="changeTest"
                    v-model="item.guidance_price_auto"
                    type="number"
                    class="no-number"
                    placeholder="请输入自动指导价"
                  ></el-input>
                  <!-- <input
                    type="text"
                    v-model="item.guidance_price_auto"
                    placeholder="请输入自动指导价"
                  /> -->
                </td>
                <td>
                  <el-input
                    @keydown.native="inputNum"
                    @input="changeTest"
                    v-model="item.guidance_price_manual"
                    type="number"
                    class="no-number"
                    placeholder="请输入手动指导价"
                  ></el-input>
                  <!-- <input
                    type="text"
                    v-model="item.guidance_price_manual"
                    placeholder="请输入手动指导价"
                  /> -->
                </td>
              </tr>
              <tr>
                <td colspan="3">钢木</td>
              </tr>
              <tr v-for="item in gm_list_form" :key="item.spu">
                <td>{{ item.spu }}</td>
                <td>
                  <el-input
                    @keydown.native="inputNum"
                    @input="changeTest"
                    v-model="item.guidance_price_auto"
                    type="number"
                    class="no-number"
                    placeholder="请输入自动指导价"
                  ></el-input>
                  <!-- <input
                    type="text"
                    v-model="item.guidance_price_auto"
                    placeholder="请输入自动指导价"
                  /> -->
                </td>
                <td>
                  <el-input
                    @keydown.native="inputNum"
                    @input="changeTest"
                    v-model="item.guidance_price_manual"
                    type="number"
                    class="no-number"
                    placeholder="请输入手动指导价"
                  ></el-input>
                  <!-- <input
                    type="text"
                    v-model="item.guidance_price_manual"
                    placeholder="请输入手动指导价"
                  /> -->
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="btn">
          <el-button type="primary" @click="submit1">提交</el-button>
        </div>
      </div>
      <div class="yesterday_cost">
        <div>
          <table>
            <thead>
              <tr>
                <th colspan="4">
                  昨日花费
                </th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td></td>
                <td>实际花费(自动)</td>
                <td>实际花费(手动)</td>
                <td>广告比</td>
              </tr>
              <tr>
                <td colspan="4">魔片</td>
              </tr>
              <tr v-for="item in mp_list_yester" :key="item.spu + 1">
                <td>{{ item.spu }}</td>
                <td>
                  <el-input
                    @keydown.native="inputNum"
                    @input="changeTest"
                    v-model="item.actual_price_auto"
                    type="number"
                    class="no-number"
                    placeholder="请输入实际花费(自动)"
                  ></el-input>
                  <!-- <input
                    type="text"
                    v-model="item.actual_price_auto"
                    placeholder="请输入实际花费(自动)"
                  /> -->
                </td>
                <td>
                  <el-input
                    @keydown.native="inputNum"
                    @input="changeTest"
                    v-model="item.actual_price_manual"
                    type="number"
                    class="no-number"
                    placeholder="请输入实际花费(手动)"
                  ></el-input>
                  <!-- <input
                    type="text"
                    v-model="item.actual_price_manual"
                    placeholder="请输入实际花费(手动)"
                  /> -->
                </td>
                <td>
                  <el-input
                    @keydown.native="inputNum"
                    @input="changeTest"
                    v-model="item.advertisement_proportion"
                    type="number"
                    class="no-number"
                    placeholder="请输入广告比"
                  ></el-input>
                  <!-- <input
                    type="text"
                    v-model="item.advertisement_proportion"
                    placeholder="请输入广告比"
                  /> -->
                </td>
              </tr>
              <tr>
                <td colspan="4">钢木</td>
              </tr>
              <tr v-for="item in gm_list_yester" :key="item.spu + 1">
                <td>{{ item.spu }}</td>
                <td>
                  <el-input
                    @keydown.native="inputNum"
                    @input="changeTest"
                    v-model="item.actual_price_auto"
                    type="number"
                    class="no-number"
                    placeholder="请输入实际花费(自动)"
                  ></el-input>
                  <!-- <input
                    type="text"
                    v-model="item.actual_price_auto"
                    placeholder="请输入实际花费(自动)"
                  /> -->
                </td>
                <td>
                  <el-input
                    @keydown.native="inputNum"
                    @input="changeTest"
                    v-model="item.actual_price_manual"
                    type="number"
                    class="no-number"
                    placeholder="请输入实际花费(手动)"
                  ></el-input>
                  <!-- <input
                    type="text"
                    v-model="item.actual_price_manual"
                    placeholder="请输入实际花费(手动)"
                  /> -->
                </td>
                <td>
                  <el-input
                    @keydown.native="inputNum"
                    @input="changeTest"
                    v-model="item.advertisement_proportion"
                    type="number"
                    class="no-number"
                    placeholder="请输入广告比"
                  ></el-input>
                  <!-- <input
                    type="text"
                    v-model="item.advertisement_proportion"
                    placeholder="请输入广告比"
                  /> -->
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="btn">
          <el-button type="primary" @click="submit2">提交</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        channel: "",
        store: "",
        country: ""
      },
      channels: [],
      stations: [],
      countrys: [],
      data1: [],
      data2: [],
      mp_list: [],
      gm_list: [],
      getformFlag: false
      //   all_price_guidance: []
    };
  },
  computed: {
    all_price_guidance() {
      return [...this.mp_list_form, ...this.gm_list_form];
    },
    all_actual_price() {
      return [...this.mp_list_yester, ...this.gm_list_yester];
    },
    mp_list_form() {
      let arr = [];
      this.mp_list.forEach(i => {
        let obj = {};
        obj["spu"] = i;
        obj["guidance_price_auto"] = "";
        obj["guidance_price_manual"] = "";
        arr.push(obj);
      });
      return arr;
    },
    gm_list_form() {
      let arr = [];
      this.gm_list.forEach(i => {
        let obj = {};
        obj["spu"] = i;
        obj["guidance_price_auto"] = "";
        obj["guidance_price_manual"] = "";
        arr.push(obj);
      });
      return arr;
    },
    mp_list_yester() {
      let arr = [];
      this.mp_list.forEach(i => {
        let obj = {};
        obj["spu"] = i;
        obj["actual_price_auto"] = "";
        obj["actual_price_manual"] = "";
        obj["advertisement_proportion"] = "";

        arr.push(obj);
      });
      return arr;
    },
    gm_list_yester() {
      let arr = [];
      this.gm_list.forEach(i => {
        let obj = {};
        obj["spu"] = i;
        obj["actual_price_auto"] = "";
        obj["actual_price_manual"] = "";
        obj["advertisement_proportion"] = "";
        arr.push(obj);
      });
      return arr;
    }
  },
  methods: {
    changeInput1(val) {
      console.log(val);
    },
    submit1() {
      // channel  station   country   dates datas type
      let fd = new FormData();
      // 是否有渠道站点国家
      let dataArr = [];
      this.all_price_guidance.forEach(i => {
        if (i.guidance_price_auto || i.guidance_price_manual) {
          dataArr.push(i);
        }
      });
      console.log("修改了的数据", dataArr);
      if (!this.getformFlag) {
        alert("请选择渠道站点国家！");
        return;
      }
      // 没有修改数据
      if (dataArr.length === 0) return;
      let date = new Date();
      let dates =
        date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate();
      console.log("日期", dates);
      let type = "guidance_price";
      for (let key in this.form) {
        if (key === "store") {
          fd.append("station", this.form[key]);
        } else {
          fd.append(key, this.form[key]);
        }
      }
      fd.append("dates", dates);
      fd.append("datas", JSON.stringify(dataArr));
      fd.append("type", type);
      let url = "server/order_management/advertisement_guidance_synchro/";
      this.$axios.post(this._url2 + url, fd).then(res => {
        if (res.data.code === 200) {
          alert("提交成功");
          this.mp_list = [];
          this.gm_list = [];
          this.changeOpt("country");
        } else {
          alert("未提交成功！");
        }
      });
    },
    submit2() {
      // channel  station   country   dates datas type
      let fd1 = new FormData();
      // 是否有渠道站点国家
      let dataArr1 = [];
      this.all_actual_price.forEach(i => {
        if (
          i.actual_price_auto ||
          i.actual_price_manual ||
          i.advertisement_proportion
        ) {
          dataArr1.push(i);
        }
      });
      console.log("修改了的数据", dataArr1);
      if (!this.getformFlag) {
        alert("请选择渠道站点国家！");
        return;
      }
      // 没有修改数据
      if (dataArr1.length === 0) return;
      let date = new Date();
      date.setTime(date.getTime() - 24 * 60 * 60 * 1000);
      let dates =
        date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate();
      console.log("日期", dates);
      let type = "actual_price";
      for (let key in this.form) {
        if (key === "store") {
          fd1.append("station", this.form[key]);
        } else {
          fd1.append(key, this.form[key]);
        }
      }
      fd1.append("dates", dates);
      fd1.append("datas", JSON.stringify(dataArr1));
      fd1.append("type", type);
      let url = "server/order_management/advertisement_guidance_synchro/";
      this.$axios.post(this._url2 + url, fd1).then(res => {
        if (res.data.code === 200) {
          alert("提交成功");
          this.mp_list = [];
          this.gm_list = [];
          this.changeOpt("country");
        } else {
          alert("未提交成功！");
        }
      });
    },
    row_click(idx) {
      console.log(idx);
    },
    getCommon() {
      // this._url2 = "http://106.52.43.196:8003/";
      let url = "server/personnel_management/get_sign_country/";
      this.channels = [];
      this.stations = [];
      this.$axios.get(this._url2 + url).then(res => {
        this.channels.push(res.data.data1);
        this.countrys = res.data.data;
        // 第一个channel
        this.form.channel = this.channels[0];
        // console.log(this.form.channel);
        this.changeOpt("channel");
        if (this.countrys.length) {
          console.log(111);
          this.form.country = this.countrys[0];
          this.changeOpt("country");
        }
      });
    },
    changeOpt(val) {
      this.getformFlag = false;
      this.mp_list = [];
      this.gm_list = [];
      if (val === "country") {
        this.stations = [];
        this.form.store = "all";
        if (this.form.country === "all") return;
        let url = "server/personnel_management/get_sign_area/";
        this.$axios
          .get(this._url2 + url, {
            params: {
              country: this.form.country
            }
          })
          .then(res => {
            this.stations = res.data.data;
            this.form.store = this.stations[0];
            this.changeOpt("store");
          });
      } else if (val === "store") {
        if (val === "all") return;
        for (let key in this.form) {
          if (this.form[key] === "all") {
            alert("请选择渠道国家站点！");
            return;
          }
        }
        this.getformFlag = true;
        let url = "server/order_management/get_spu/";
        let queryParams = {};
        for (let key in this.form) {
          queryParams[key] = this.form[key];
        }
        this.$axios
          .get(this._url2 + url, {
            params: queryParams
          })
          .then(res => {
            console.log(res);
            this.mp_list = res.data.mp_list;
            this.gm_list = res.data.gm_list;
          });
      }
    },
    changeTest() {
      this.$forceUpdate();
    },
    inputNum(e) {
      let key = e.key;
      // 不允许输入'e'和'.'
      if (key === "e" || key === "-") {
        e.returnValue = false;
        return false;
      }
      return true;
    }
  },
  created() {
    this.getCommon();
  }
};
</script>

<style scoped>
#synchronous_advertising_guidance {
  display: flex;
  height: 88vh;
  overflow: hidden;
}
.leftMenu {
  background-color: rgba(255, 255, 255, 0.9);
  text-align: center;
  padding-top: 22px;
  flex: 0 0 350px;
}
.rightTable {
  flex: 0 0 70vw;
  margin: 0 auto;
  background-color: #fff;
  text-align: center;
  padding-bottom: 20px;
  overflow-y: scroll;
}
#synchronous_advertising_guidance table {
  border-collapse: collapse;
  width: 100%;
  background: transparent;
}
#synchronous_advertising_guidance td {
  width: 300px;
  border: 1px solid;
  height: 32px;
}
#synchronous_advertising_guidance input[type="text"] {
  padding: 5px 20px;
  border: none;
  width: 100%;
  height: 100%;
  background-color: transparent;
}
#synchronous_advertising_guidance th {
  padding: 20px 0;
}
#synchronous_advertising_guidance .btn {
  margin-top: 20px;
}
</style>
<style>
/* el-input number类型去除右侧 */
#synchronous_advertising_guidance .el-input.no-number {
  -moz-appearance: textfield;
}
#synchronous_advertising_guidance .el-input.no-number input[type="number"] {
  -moz-appearance: textfield;
}

#synchronous_advertising_guidance
  .el-input.no-number::-webkit-outer-spin-button,
#synchronous_advertising_guidance
  .el-input.no-number::-webkit-inner-spin-button {
  margin: 0;
  -webkit-appearance: none !important;
}

#synchronous_advertising_guidance
  .el-input.no-number
  input[type="number"]::-webkit-outer-spin-button,
#synchronous_advertising_guidance
  .el-input.no-number
  input[type="number"]::-webkit-inner-spin-button {
  margin: 0;
  -webkit-appearance: none !important;
}
</style>



